<template>
    <div id="service">
        <Head></Head>
        <div class="service-top">
            <div class="service-top1">
                <img :src="this.serviceimg1" alt="">
            </div>
        </div>
        <p class="service-title">
            <span>我们能做什么？</span>
            What can we do?</p>
        <div class="container service-list">
            <div class="row">
                <ul>
                    <li class="service-con-left col-md-12 ">
                        <div class="service-list-img col-md-7 ">
                            <img :src="this.serviceList1.service_img" alt="">
                        </div>
                        <div class="service-list-txt col-md-5 ">
                            <h2>{{this.serviceList1.service_title}}</h2>
                            <p>{{this.serviceList1.service_desc}}
                            </p>
                        </div>
                    </li>
                    <li class="service-con-left col-md-12 ">
                        <div class="service-list-img col-md-7 ">
                            <img :src="this.serviceList2.service_img" alt="">
                        </div>
                        <div class="service-list-txt col-md-5 ">
                            <h2>{{this.serviceList2.service_title}}</h2>
                            <p>{{this.serviceList2.service_desc}}
                            </p>
                        </div>
                    </li>
                    <li class="service-con-right col-md-12 ">
                        <div class="service-list-txt col-md-5 ">
                            <h2>{{this.serviceList3.service_title}}</h2>
                            <p>{{this.serviceList3.service_desc}}
                            </p>
                        </div>
                        <div class="service-list-img col-md-7 ">
                            <img :src="this.serviceList3.service_img" alt="">
                        </div>
                    </li>
                    <li class="service-con-right col-md-12 ">
                        <div class="service-list-txt col-md-5 ">
                            <h2>{{this.serviceList4.service_title}}</h2>
                            <p>{{this.serviceList4.service_desc}}
                            </p>
                        </div>
                        <div class="service-list-img col-md-7 ">
                            <img :src="this.serviceList4.service_img" alt="">
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <bottomBar></bottomBar>
        <canvasBg></canvasBg>
    </div>
</template>
<script>
    import bottomBar from './components/bottomBar'
    import canvasBg from './components/canvasBg'
    import Head from './components/Head'
    export default {
        name: 'Service',
        components: {
            bottomBar,
            canvasBg,
            Head
        },
        data:function(){
            return{
                serviceLists:'',
                serviceList1:'',
                serviceList2:'',
                serviceList3:'',
                serviceList4:'',
                serviceimg1:'',
                serviceimg2:'',
                serviceimg3:''
            }
        },
        created(){
            this.serviceF();
        },
        methods:{
            serviceF:function(){
                this.$http.get('http://adwww.ldcywh.com/index.php/index/Api/service')
                .then( (res) => {
                     this.serviceLists=res.data.data.service_list;

                     this.serviceimg1=res.data.data.banner[0].bannerUrl;
                     // this.serviceimg2=res.data.data.banner[1].bannerUrl;
                     // this.serviceimg3=res.data.data.banner[2].bannerUrl;
                     this.serviceList1=res.data.data.service_list[0];
                     this.serviceList2=res.data.data.service_list[1];
                     this.serviceList3=res.data.data.service_list[2];
                     this.serviceList4=res.data.data.service_list[3];
                     // console.log(res.data)
                })
            }
        }
    }
    $(".main-ym").on("click",function(){
        window.location.href ='./index.html'
    });
    $(".service-ym").on("click",function(){
        window.location.href ='./service.html'
    });
    $(".about-ym").on("click",function(){
        window.location.href ='./about.html'
    });
    $(".anlie-ym").on("click",function(){
        window.location.href ='./case.html'
    });

</script>

<style laang="less">
body,p,h1,h2,h3,h4,span{font-family: "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei";}
html{font-family: "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei";}
body{margin:0;padding:0;}
li{list-style-type: none;}
#service{text-align: center;}
.main-ym{color: #000 !important;}
.service-ym{color: #fff !important;}
.service-top{position: relative;width: 100%;margin-top: 56px;}
.service-top2,.service-top3{position: absolute;bottom: 0;}
.service-top2{left: -474px;
    -webkit-animation:service-top2 15s linear alternate  ;
    animation:service-top2 15s linear alternate  ;
}
@-webkit-keyframes service-top2 {
    0% { transform:translateX(0px);}
    100%{ transform:translateX(500px);}
}

@keyframes service-top2 {
    0% { transform:translateX(0px);}
    100%{ transform:translateX(500px);}
}
.service-top1 img,.service-top3 img{width: 100%;}
.service-title{text-align: center;font-size: 2.5rem;padding: 50px 0;margin: 0;}
.service-title span{display: inherit;font-size: 1.6rem;}
#service ul{padding: 0px;margin: 0;}
.service-con-left{border-bottom: 1px solid #dcdcdc;overflow: hidden;}
.service-con-left div,.service-con-right div{display: inline-block;float: left;}
.service-con-right{border-bottom: 1px solid #dcdcdc;overflow: hidden;}
.service-list{border-top: 1px solid #dcdcdc;}
.service-con-left .service-list-img{float: left;margin:55px 0;}
.service-con-right .service-list-img{margin:55px 0;}
.service-list-img img{width: 100%;}
.service-list-txt{padding-top: 95px;}
.service-list-txt h2{font-size: 1.5rem;text-align: left;margin-bottom: 25px;
    letter-spacing: 5px;}
.service-list-txt p{color: #424141;text-indent:2em;text-align: left;}


</style>



